@media (min-width: 1440px) {
    header {
      max-width: 1440px;
    }
  }
.filters-group {
  display: flex;
  flex-wrap: wrap;
}
.filter-item {
  padding: 1px 15px;
  background: #bae594;
  border-radius: 5px;
  font-weight: 300;
  font-size: 0.9em;
  text-transform: capitalize;
  flex: 1 1 40%;
  max-width: 40%;
  text-align: center;
  margin: 3px auto;
  cursor: pointer;
}

.filter-item.active {
  background: #61c200;
}

aside {
  position: -webkit-sticky;
  position: sticky;
  background: white;
  padding: 20px;
  top: 20px;
  margin-top: 20px;
  border-radius: 10px;
}

aside h1 {
  font-size: 1.5em;
}

aside h2 {
  font-size: 1em;
  text-align: center;
  margin-top: 5px;
}

aside hr {
  padding: 0;
  margin-bottom: 0.5rem;
  margin-top: 0;
}

.clear-filter {
  text-align: center;
  background: blue;
  border-radius: 5px;
  max-width: 50%;
  margin: 5px auto;
  color: white;
  font-weight: 300;
  font-size: 0.8em;
  border: none;
}
.clear-filter:hover {
  background-color: red;
  cursor: pointer;
}

.home {
  background: #efeff4;
  min-height: 100vh;
  padding-top: 20px;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  margin: auto;
  margin-top: 20%;
}

.no-launches {
  color: grey;
  font-size: 3em;
  margin: auto;
  margin-top: 20%;
}

.custom-container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.custom-row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.launch-card,
.sidebar-col,
.main-area-col {
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}

.launch-card {
  flex: 0 0 100%;
  max-width: 100%;
}

.sidebar-col {
  flex: 0 0 100%;
  max-width: 400px;
  margin: 0 auto;
}

.main-area-col {
  flex: 0 0 100%;
  max-width: 100%;
  padding-bottom: 30px;
}


/* tablet */
@media (min-width: 700px) {
  .launch-card {
    flex: 0 0 50%;
    max-width: 50%;
  }
  .sidebar-col {
    flex: 0 0 30%;
    max-width: 30%;
  }

  .main-area-col {
    flex: 0 0 70%;
    max-width: 70%;
  }
}

/* desktop */
@media (min-width: 1025px) {
  .launch-card {
    flex: 0 0 25%;
    max-width: 25%;
  }
  .sidebar-col {
    flex: 0 0 20%;
    max-width: 20%;
  }

  .main-area-col {
    flex: 0 0 80%;
    max-width: 80%;
  }
}

@media (min-width: 1440px) {
  .max-width-xl {
    max-width: 1440px;
    margin: 0 auto;
  }
  header {
    max-width: 1440px;
  }
}


@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #efeff4;
    padding-top: 10px;
    padding-left: 10px;
    text-align:center;
  }

  footer h2 {
      font-size: 1.5em;
  }

  footer h2 span {
      font-weight: bold;
  }
.mission-patch {
  width: 80% !important;
  margin: auto;
  background: #ececec;
}

.card {
  margin: 20px 0;
}

.launch-card {
  display: flex;
  flex-direction: column;
}

.launch-card .card {
  padding-top: 20px;
  border: none;
  border-radius: 10px;
  flex: 1 0 auto;
}

.color-blue {
  color: #3f4c9d;
}

.mission-name {
  font-weight: bold;
}

.detail.bold {
  font-weight: bold;
}

